import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    name: '', // это то, что вводим в инпут
    userInfo: null,
    posts: null,
  },
  getters: {
    
  },
  mutations: {
    setName(state, payload) {
      state.name = payload
    },
    userInfo(state, payload) {
      state.userInfo = payload;
      state.name = ''
    },
    getRepos(state, payload) {
      state.posts = payload
    }
    
  },
  actions: {
    async getUser(context, name) {
      
      try {
        let response = await axios.get(`https://api.github.com/users/${name}`)
        this.commit('userInfo', response.data)
        let response2 = await axios.get(`https://api.github.com/users/${name}/repos`)
        this.commit('getRepos', response2.data)
        
      } catch (error) {
        console.error('Ошибка произошла при получении данных о пользователях', error);
      }
    },
    
  },
  
})
