<template>
    <div class="search">
        <input 
            class="search__info" 
            type="text" 
            placeholder="Введите имя пользователя" 
            v-model="getUsers"
            
        >
        <button class="search__btn" @click="getUser(getUsers) " >
            <p>НАЙТИ</p>
        </button>
        
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations, mapGetters } from 'vuex'
    export default {
        computed: {
            ...mapState(['name']),
           /*  ...mapGetters(['sortName']), */
            getUsers: {
                get() {
                    return this.name
                },
                set(val) {
                   this.setName(val)
                },
            }
        },
        methods: {
            ...mapMutations(['setName']),
            ...mapActions(['getUser', 'getRepos'])
        },
    }
</script>

