<template>
    <div class="sorted">
        <h2>Сортировка</h2>
        <div class="sorted__btns">
            <button @click="sortName">ИМЯ</button>
            <button @click="sortStars">ЗВЕЗДЫ</button>
            <button @click="sortDate">ДАТА</button>
        </div>
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex'

    export default {
        computed: {
            ...mapState(['posts'])
        },
        methods: {
            sortName() {
                return this.posts.sort((prev,next) => prev.name.localeCompare(next.name))
            },
            sortStars() {
                return this.posts.sort((prev,next) => prev.stargazers_count - next.stargazers_count)
            },
            sortDate() {
                return this.posts.sort((prev,next) => new Date(prev.pushed_at) - new Date(next.pushed_at))
            }
        },
        
    }
</script>

